/* Experience.css */
  
  .experience h2 {
    text-align: center;
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .experience-list {
    list-style-type: none;
    padding: 0;
  }
  
  .experience-item {
    margin-bottom: 20px;
  }
  
  .experience-item strong {
    font-size: 1.1rem;
    color: #2d3748; /* Dark gray for company names */
  }
  
  .experience-item ul {
    list-style-type: none;
    padding-left: 20px;
  }
  
  .experience-item li {
    font-size: 1rem;
    line-height: 1.5;
    color: #4a5568; /* Lighter gray for descriptions */
  }
  
  .experience-item ul li::before {
    content: "â€¢";
    margin-right: 8px;
    color: #2b6cb0; /* Blue bullet points */
  }
  
  @media (max-width: 600px) {
    .experience {
      padding: 15px;
      margin: 20px;
    }
  
    .experience h2 {
      font-size: 1.5rem;
    }
  
    .experience-item strong {
      font-size: 1rem;
    }
  
    .experience-item ul li {
      font-size: 0.9rem;
    }
  }
  