/* About.css */
  
  /* Title styling */
  .title {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 20px;
    word-wrap: break-word; /* Ensure the title doesn't overflow */
  }
  
  /* Paragraph styling */
  .description {
    font-size: 1.125rem;
    line-height: 1.75rem;
    font-family: 'serif'; /* equivalent to font-serif */
    margin-top: 20px;
    padding: 0 10px; /* Add padding to avoid text touching the sides */
  }
  
  /* Profile image styling */
  .profile-image {
    display: block;
    margin: 10px auto;
    border-radius: 50%; /* Makes the image circular */
    width: 150px; /* Ensures the image has equal width and height */
    height: 150px; /* Ensures the image has equal width and height */
    object-fit: cover;
  }
  
  
  /* Button container styling */
  .button-container {
    display: flex;
    flex-direction: row;
    gap: 16px; /* Reduced gap for better spacing */
    margin-top: 40px;
    justify-content: center;
    flex-wrap: wrap; /* Allow buttons to wrap on small screens */
    width: 100%;
  }
  
  /* Button styling */
  .button {
    background-color: #2d3748; /* equivalent to bg-gray-800 */
    color: white;
    padding: 12px 24px;
    border-radius: 0.5rem;
    text-decoration: none;
    transition: background-color 0.3s;
    width: auto;
    min-width: 120px; /* Make sure buttons have a minimum width */
    text-align: center;
    margin: 5px; /* Add spacing between buttons */
  }
  
  .button:hover {
    background-color: #4a5568; /* equivalent to hover:bg-gray-700 */
  }
  
  /* Media Query for smaller screens */
  @media (max-width: 600px) {
    .title {
      font-size: 1.5rem; /* Smaller title for small screens */
    }
  
    .description {
      font-size: 1rem;
      padding: 0 15px;
    }
  
    .profile-image {
      width: 120px; /* Smaller image for small screens */
    }
  
    .button-container {
      flex-direction: column; /* Stack buttons vertically on smaller screens */
      gap: 10px; /* Less gap between buttons */
    }
  
    .button {
      width: 100%; /* Make buttons full-width on small screens */
      padding: 12px;
    }
  }
  